import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col, Button } from 'antd';
import { returnDoc } from 'dump/data';

const columns = [
  {
    title: 'Mã sv',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      returnDoc.length >= 1 ? (
        <Fragment>
          <Button
            className="btnNoiNgoaiTru"
            type="primary"
            href={`#return-doc/form/${record.code}`}
          >
            Hoàn trả
          </Button>
        </Fragment>
      ) : null,
  },
];

class ReturnDocPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Trả giấy tờ">
        {/* <Row>
          <Col span={24}>
            <BtnAddNew href="#/teacher/form/new" />
          </Col>
        </Row> */}
        <Row>
          <Col span={24}>
            <Table bordered dataSource={returnDoc} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReturnDocPage />
  </Page>
);



